@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background {
  background-image: url(/bettersurveys/static/media/double-bubble-dark.9b90d46a.png);
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.counter {
  text-align: center;
  min-width: 3vw;
  margin-left: 1vw;
  margin-right: 1vw;
}

.questionText {
  width: 80%;
  justify-content: flex-start;
  display: flex;
  color: white;
  font-family: "Lato", sans-serif;
}

.textinput {
  word-break: break-all;
  display: block;
  min-height: 5vh;
  width: 80%;
  margin: 1vh 0vh;
  background-color: transparent;
  border: 1px solid;
  border-color: transparent transparent rgb(255, 255, 255, 0.5) transparent;
  color: white;
  font-size: 5vh;
  overflow: hidden !important;
}

.textinput:focus {
  outline: none;
  border-color: transparent transparent white transparent;
}

.radioButtonGrid {
  display: grid;
  grid-template-columns: 50% 50%;
  height: 50vh;
  grid-column-gap: 1vw;
  grid-row-gap: 1vw;
}
.radioButton {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  font-size: 3vw;
  color: white;
  border: 1px solid white;
  border-radius: 1vh;
}

.active {
  color: black !important;
  background-color: white !important;
}

.scale {
  display: flex;
  justify-content: space-between;
}

.scaleValue {
  background-color: transparent;
  font-size: 5vh;
  color: white;
  border: 2px solid white;
  border-radius: 1vh;
  margin: 1vh 2vh 1vh 0vh;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 20vh;
}

.submit {
  background-color: transparent;
  border: 2px solid white;
  margin: 5vh 2vh 3vh 0vh;
  padding: 1vh 2vh;
  height: 5vh;
  color: white;
}

.inputContainer {
  width: 80%;
  margin-left: 5vw;
  margin-right: 5vw;
}

.submit:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.questionContainer {
  width: 50%;
  height: 80%;
  background-color: RGB(0, 0, 0, 0.2);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  border: 1px solid white;
}

